<template>
    <div class="search">
        <Header :isLeft="true" title="搜索"></Header>
        <div class="search_header">
            <form action="" class="search_wrap">
                <i class="fa fa-search"></i>
                <input type="text" v-model="key_word" placeholder="输入商家商家信息">
                <button @click.prevent="searchHandle">搜索</button>
            </form>
        </div>
        <!-- //搜索商家列表 -->
        <div class="shop" v-if="result && !showShop">
            <div class="empty_wrap" v-if="empty">
                <!--如果没有搜索的结果会显示这个页面-->
                <img src="https://fuss10.elemecdn.com/d/60/70008646170d1f654e926a2aaa3afpng.png" alt>
                <div class="empty_txt">
                    <h4>附近没有搜索结果</h4>
                    <span>换个关键词试试吧</span>
                </div>
            </div>
            <div v-else>
                <SearchIndex @showClick="$router.push('/shop')" :data="result.restaurants" ></SearchIndex>
                <SearchIndex @showClick="shopItemClick" :data="result.words"></SearchIndex>
            </div>
        </div>
        <!-- 点击搜索列表展示出的内容 -->
        <div class="container" v-if="showShop" >
            <!-- 导航栏 -->
            <FilterView :filterData="filterData" @update="update"></FilterView>
            <!-- 商家列表 -->
            <div class="shoplist" v-infinite-scroll="loadMore" :infinite-scroll-disabled="loading">
                <IndexShop v-for="(item,index) in restaurants" :key="index" :restaurant="item.restaurant"></IndexShop>
            </div>
        </div>

    </div>
</template>
<script>
    
    import Header from '../components/Header'
    import SearchIndex from '../components/SearchIndex'
    import FilterView from '../components/FilterView'
    import IndexShop from '../components/IndexShop'
    import { InfiniteScroll } from 'mint-ui'



    export default {
        name: 'search',
        data() {
            return {
                //搜索关键字
                key_word: '',
                result: null,
                //控制服务器有没有搜索的内容
                empty: false,
                //点击搜索列表页面默认隐藏
                showShop: false,
                //导航信息
                filterData: null,
                //向服务器传的数据（排序的数据）
                data: null,
                // 商家列表信息
                restaurants: [],
                page: 0,
                size: 7,
                // loading 为false时候说明还有信息
                loading: false,
            }
        },
        created() {
            //导航栏筛选
            this.$axios.get('/api/profile/filter')
                .then(res => {
                    // console.log(res.data);
                    this.filterData = res.data;

                });


        },
        components: {
            Header,
            SearchIndex,
            FilterView,
            IndexShop
        },
        watch: {
            // 监听搜索
            key_word() {
                this.empty = false;
                this.keyWordChange();
                this.showShop = false;

            }

        },
        methods: {
            //搜索
            keyWordChange() {
                console.log(this.key_word);
                this.$axios.get(`/api/profile/typeahead/${this.key_word}`)
                    .then(res => {
                        console.log(res.data);
                        this.result = res.data;
                        console.log(this.result);

                    })
                    .catch(err => {
                        this.result = null;
                    })
            },
            //搜索按钮
            searchHandle() {
                // 1.搜索框为空
                // 2.搜索框不为空
                //     判断搜索内容服务器是否存在，存在就跳转
                //     不存在就跳转到空页面
                if (!this.key_word) return;
                if (this.result && (this.result.restaurants.length > 0 || this.result.restaurants.words > 0)) {
                    this.shopItemClick();
                } else {
                    this.empty = true;
                }
            },
            //点击搜索结果的列表触发的事件
            shopItemClick() {
                this.page = 0;
                this.restaurants =[];
                this.showShop = true;
            },
            //更新数据
            update(condation) {
                // console.log(condation)
                this.data = condation;
                this.shopItemClick();

            },
            // 信息列表可以无限拉新
            loadMore() {
                console.log(1);
                this.page++;
                // 商家信息
                this.$axios.post(`/api/profile/restaurants/${this.page}/${this.size}`, this.data)
                    .then(res => {
                        // console.log(res);
                        // this.restaurants = res.data;
                        if(res.data.length>0){
                            res.data.forEach(item=>{
                                this.restaurants.push(item);
                            })
                            
                        }else{
                            this.loading = true;//商家信息加载完毕
                        }
                    })
            }
        },

    }

</script>
<style scoped>
    .search {
        width: 100%;
        height: 100%;
        overflow: auto;
        box-sizing: border-box;
    }

    .search_header {
        margin-top: 45px;
        background: #fff;
        padding: 0 4.266667vw;
    }

    .search_header .search_wrap {
        padding: 2.933333vw 2.933333vw 2.933333vw 0;
        display: flex;
        align-items: center;
        position: relative;
    }

    .search_wrap .fa-search {
        width: 2.933333vw;
        height: 2.933333vw;
        color: #888;
        position: absolute;
        top: 4.6666666vw;
        left: 2.933333vw;
    }

    .search_wrap input {
        flex-grow: 1;
        border-radius: 0.266667vw;
        background-color: #f8f8f8;
        padding: 1.733333vw 4vw 1.733333vw 8.8vw;
        color: #666;
        outline: none;
        border: none;
    }

    .search_wrap button {
        outline: none;
        border: none;
        color: #333;
        font-size: 0.426667rem;
        background: #fff;
        font-weight: 700;
        margin-left: 2.933333vw;
        font-size: 14px;
    }

    .shop {
        width: 100%;
        height: calc(100% - 95px);
        overflow: auto;
    }

    .empty_wrap {
        width: 100%;
        height: 100%;
        overflow: hidden;
        background: #fff;
        display: flex;
        justify-content: center;
    }

    .empty_wrap img {
        width: 35vw;
        height: 35vw;
    }

    .empty_txt h4 {
        color: #666;
        font-size: 1rem;
        margin: 12vw 0 2vw 0;
    }

    .empty_txt span {
        color: #999;
        font-size: 0.8rem;
    }
</style>